<template>
  <div class="home">
    <v-text-field
      @click:append="putNewList"
      @keyup.enter="putNewList"
      v-model="newTodoitem"
      clearable
      outlined
      label="Nueva Tarea"
      append-icon="mdi-send"
      hide-details
      class="text-field"
    ></v-text-field>

    <v-list>
      <v-list-item-group multiple>
        <v-list-item
          v-for="item in todoList"
          :key="item.id"
          @click="item.state = !item.state"
        >
          <template>
            <v-list-item-action>
              <v-checkbox
                :input-value="item.state"
                color="primary"
              ></v-checkbox>
            </v-list-item-action>

            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon @click.stop="deleteTodoItem(item.id)">mdi-delete</v-icon>
            </v-list-item-icon>
          </template>
        </v-list-item>
      </v-list-item-group>
    </v-list>

  </div>
</template>

<script>
export default {
  data: () => ({
    newTodoitem: null,
    todoList: [
      { id: 1, text: "eat bananas", state: false },
      { id: 2, text: "eat pinia", state: false },
      { id: 3, text: "eat mangos", state: false },
      { id: 4, text: "eat something else", state: false },
    ],
  }),
  methods: {
    putNewList() {
      if (this.newTodoitem) {
        let todoVar = { id: Date.now(), text: this.newTodoitem, state: false };
        this.todoList.push(todoVar);
        this.newTodoitem = null;
      }
    },
    deleteTodoItem(id) {
      this.todoList = this.todoList.filter((item) => item.id != id);
    },
  },
};
</script>
<style lang="scss" >
.home {
  .text-field{
    margin: 15px auto !important;
  }
  .mdi-delete:hover {
    color: var(--color-danger) !important;
  }
  .mdi-send:hover {
    color: var(--color-black) !important;
  }
}
</style>